import MenuIcon from '@mui/icons-material/Menu'
import PrintIcon from '@mui/icons-material/Print'
import {
  AppBar,
  Box,
  Divider,
  Drawer,
  IconButton,
  List,
  ListItem,
  ListItemButton,
  ListItemIcon,
  ListItemText,
  Stack,
  styled,
  Switch,
  Toolbar,
  Typography,
} from '@mui/material'
import React from 'react'
import { FormattedMessage } from 'react-intl'

import { SAVED_LOCALE_KEY } from '../../App'
import { useLocalStorage } from '../../hooks'
import FranceFlagIcon from '../../shared-resources/france-flag-icon.svg'
import UnitedKingdomFlagIcon from '../../shared-resources/united-kingdom-flag-icon.svg'
import { Locale } from '../../types'

type Props = {
  locale: Locale
  setLocale: (locale: Locale) => void
  children: React.ReactNode
}

const drawerWidth = 240
const navItems = ['Home', 'About', 'Contact']

const LocaleSwitch = styled(Switch)(() => ({
  width: 62,
  height: 34,
  padding: 7,

  '& .MuiSwitch-switchBase': {
    margin: 1,
    padding: 0,
    transform: 'translateX(6px)',
    '&.Mui-checked': {
      color: '#fff',
      transform: 'translateX(22px)',
      '& + .MuiSwitch-track': {
        opacity: 1,
        backgroundColor: '#aab4be',
      },
    },
  },

  '& .MuiSwitch-thumb': {
    backgroundColor: '#001e3c',
    width: 32,
    height: 32,
  },

  '& .MuiSwitch-track': {
    opacity: 1,
    backgroundColor: '#aab4be',
    borderRadius: 20 / 2,
  },
}))

const Header = ({ locale, setLocale, children }: Props) => {
  const [, setSavedLocale] = useLocalStorage<Locale>(SAVED_LOCALE_KEY, 'en')
  const [isMobileOpen, setIsMobileOpen] = React.useState(false)

  const handleDrawerToggle = () => {
    setIsMobileOpen((prevState) => !prevState)
  }

  const toggleLocale = () => {
    const newLocale = locale === 'en' ? 'fr' : 'en'
    setLocale(newLocale)
    setSavedLocale(newLocale)
  }

  const drawer = (
    <Box onClick={handleDrawerToggle} sx={{ textAlign: 'center' }}>
      <Typography variant="h6" sx={{ my: 2 }}>
        Joris Harnetiaux
      </Typography>
      <Divider />
      <List>
        {navItems.map((item) => (
          <ListItem key={item} disablePadding>
            <ListItemButton sx={{ textAlign: 'center' }}>
              <ListItemText primary={item} />
              <ListItemIcon>
                <PrintIcon />
                <FormattedMessage id="print" defaultMessage="Print" />
              </ListItemIcon>
            </ListItemButton>
          </ListItem>
        ))}
      </List>
    </Box>
  )

  const container = window.document.body

  return (
    <>
      <Box sx={{ display: 'flex' }}>
        <AppBar component="nav">
          <Toolbar sx={{ justifyContent: 'space-between' }}>
            <IconButton
              color="inherit"
              aria-label="open drawer"
              edge="start"
              onClick={handleDrawerToggle}
              sx={{ mr: 2, display: { sm: 'none' } }}
            >
              <MenuIcon />
            </IconButton>
            <Typography
              variant="h6"
              component="div"
              sx={{ flexGrow: 1, display: { xs: 'none', sm: 'block' }, lineHeight: 0.9 }}
            >
              Joris
              <br />
              &nbsp;&nbsp;Harnetiaux
            </Typography>
            {/* <Box sx={{ display: { xs: 'none', sm: 'block' } }}>
              {navItems.map((item) => (
                <Button key={item} sx={{ color: '#fff' }}>
                  {item}
                </Button>
              ))}
            </Box> */}
            <Stack direction="row" spacing={1} sx={{ alignItems: 'center' }}>
              <Box component="img" src={UnitedKingdomFlagIcon} alt="EN" width={24} />
              <LocaleSwitch checked={locale === 'fr'} onChange={toggleLocale} />
              <Box component="img" src={FranceFlagIcon} alt="FR" width={24} />
            </Stack>
          </Toolbar>
        </AppBar>
        <nav>
          <Drawer
            container={container}
            variant="temporary"
            open={isMobileOpen}
            onClose={handleDrawerToggle}
            ModalProps={{
              keepMounted: true, // Better open performance on mobile.
            }}
            sx={{
              display: { xs: 'block', sm: 'none' },
              '& .MuiDrawer-paper': { boxSizing: 'border-box', width: drawerWidth },
            }}
          >
            {drawer}
          </Drawer>
        </nav>
      </Box>
      <Box>{children}</Box>
    </>
  )
}

export default Header
